@import './colors.css';

Event {
  @apply article;
  width: calc(100% - 16px);
  max-width: 400px;
  background: white;
  margin: 8px auto;
}

Image {
  height: 140px;
  background-size: cover;
  background-position: center;
}

Content {
  padding: 18px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

Details {
  -ms-flex: 1 1;
      flex: 1 1;
}

Details h3 {
  font-size: 1rem;
}

Aside {
  padding: 8px 12.5px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 0 1;
      flex: 0 1;
}

StyledDate {
  font-size: 2.25rem;
}

Month {
  color: var(--grey-600);
  font-size: .9em;
}

Time {
  @apply time;
  color: var(--grey-700);
  font-weight: 600;
  margin-top: 25px;
}

Tags {
  margin-top: 24px;
}

Tag {
  @apply span;
  padding: 4px 9px;
  font-size: 13px;
  background: var(--grey-200);
  color: var(--grey-600);
  margin: 8px 0 0 8px;
}

Entrance {
  @apply p;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  font-size: .8rem;
  font-style: italic;
  margin: 0;
  padding: 0 12px 18px;
}

Footer {
  padding: 0 16px 16px;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
  display: -ms-flexbox;
  display: flex;
}

Button {
  @apply button;
  padding: 4px 8px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font: inherit;
  background: none;
  border: none;
  outline: none;
}

Button[type="flat"] {
  color: var(--indigo-500);
  background: transparent;
  font-weight: 600;
}

Button[type="flat"]:hover {
  background: rgba(0, 0, 0, 0.1);
}

Button[type="flat"]:active {
  background: rgba(0, 0, 0, 0.2);
}



