@import './colors.css';

body {
  overflow: hidden;
}

body, #root {
  width: 100%;
  height: 100%;
}

App {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

Header {
  @apply header;
  background: var(--indigo-500);
  overflow: hidden;
}

Header Logo {
  width: 250px;
  height: 70px;
  margin: 37px auto;
  background-image: url('./logo.svg');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

Header nav {
  max-width: 30em;
  font-size: 0.8rem;
  margin: auto;
}

Header nav ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  list-style: none;
  margin: 0;
  padding: 0;
}

Header nav NavItem {
  @apply li;
  padding: 15px 0;
  color: white;
  opacity: 0.7;
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

Header nav NavItem:active {
  background: rgba()
}

Header nav NavItem[selected] {
  opacity: 1;
}

Indicator {
  width: calc(100% / attr(of number, 1));
  height: 2px;
  background: var(--yellow-400);
  -webkit-transform: translateX(calc(attr(index number) * -100%));
      -ms-transform: translateX(calc(attr(index number) * -100%));
          transform: translateX(calc(attr(index number) * -100%));
  -webkit-transition: -webkit-transform .15s;
  transition: -webkit-transform .15s;
  -o-transition: transform .15s;
  transition: transform .15s;
  transition: transform .15s, -webkit-transform .15s;
}

Events {
  -ms-flex: 1 1;
      flex: 1 1;
  overflow: scroll;
}



